<template>
	<my-search @handle-click="gotoSearch"></my-search>
	<view class="scroll-view-container">
		<!-- 左侧滑动区域 -->
		<scroll-view class="left-scroll-view" scroll-y="true" :style="{ height: wh + 'px' }">
			<block v-for="(item, i) in cateList" :key="i">
				<view @click="activeChange(i)" :class="['left-scroll-view-item', i === active ? 'active' : '']">{{ item.cat_name }}</view>
			</block>
		</scroll-view>
		<!-- 右侧滑动区域 -->
		<scroll-view :scroll-top="scrollTop" scroll-y="true" :style="{ height: wh + 'px' }">
			<view class="cate-level2" v-for="(item2, i2) in cateLevel2" :key="i2">
				<!-- 二级分类 -->
				<view class="cate-level2-title">-{{ item2.cat_name }}-</view>
				<!-- 二级分类下的三级分类 -->
				<!-- 三级分类列表 -->
				<view class="cate-level3-list">
					<!-- 三级分类的item项 -->
					<view class="cate-level3-item" v-for="(item3, i3) in item2.children" :key="i3" @click="gotoGoodsList(item3)">
						<image :src="item3.cat_icon.includes('api-hmugo-web.itheima.net') ? item3.cat_icon : `${BASE_URL}${item3.cat_icon}`"></image>
						<text>{{ item3.cat_name }}</text>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script setup>
import { ref } from 'vue';
import { onLoad, onShow } from '@dcloudio/uni-app';
import { setBarge } from '@/hook/useTabbarBadge.js';
import { ajaxGet, fetchGet } from '@/utils/api.js';
import { BASE_URL } from '@/utils/config.js';

const cateList = ref([]);
const cateLevel2 = ref([]);

const getCateListData = async () => {
	const { data: res } = await ajaxGet('/api/public/v1/cate/categories');
	const { meta, message } = res;
	if (meta.status !== 200) uni.$showMessage();
	cateList.value = message;
	cateLevel2.value = message[0].children;
};

const active = ref(0);
const scrollTop = ref(0);
const activeChange = i => {
	active.value = i;
	// 更新二级分类数据
	cateLevel2.value = cateList.value[i].children;
	// 回到顶部
	scrollTop.value = scrollTop.value === 0 ? 1 : 0;
};

const gotoGoodsList = ({ cat_id }) => {
	uni.navigateTo({
		url: '/subpkg/goods_list/goods_list?cid=' + cat_id
	});
};

const gotoSearch = () => {
	uni.navigateTo({
		url: '/subpkg/search/search'
	});
};

const wh = ref(0); //当前屏幕可使用高度
onLoad(async () => {
	const { windowHeight } = await uni.getSystemInfo();
	wh.value = windowHeight - 50;
	getCateListData();
});

onShow(() => {
	setBarge();
});
</script>

<style lang="scss">
.scroll-view-container {
	display: flex;
	.left-scroll-view {
		width: 120px;
		.left-scroll-view-item {
			background: #f7f7f7;
			font-size: 12px;
			line-height: 60px;
			text-align: center;
			&.active {
				background: #ffffff;
				position: relative;
				&::before {
					content: ' ';
					display: block;
					width: 3px;
					height: 30px;
					background: #f2ba4b;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
	}
	.cate-level2-title {
		text-align: center;
		font-size: 12px;
		font-weight: bold;
		padding: 15px 0;
	}
	.cate-level3-list {
		display: flex;
		flex-wrap: wrap;
		.cate-level3-item {
			width: 33.33%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin-bottom: 10px;
			image {
				width: 60px;
				height: 60px;
			}
			text {
				font-size: 12px;
			}
		}
	}
}
</style>
